import axios from 'axios';
import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import '../styles/Login.css'

const Main = () => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [pw, setPassword] = useState('');
    const [confirmPW, setConfirmPW] = useState('');
    const refInput = useRef();

    useEffect(() => {
        refInput.current.focus();
    }, [])

    const navigate = useNavigate();

    const toLogin = () => {
        navigate('/login');
    }

    const onNameHandler = (event) => {
        setName(event.currentTarget.value);

    };
    const onEmailHandler = (event) => {
        setEmail(event.currentTarget.value);
    };
    const onPasswordHandler = (event) => {
        setPassword(event.currentTarget.value);
    };
    const onConfirmPWHandler = (event) => {
        setConfirmPW(event.currentTarget.value);
    };


    const onSubmitHandler = (event) => {
        event.preventDefault();
        if (pw !== confirmPW) {
            return alert('   .')
        }
        axios.post("http://localhost:3001/", { name, email, pw }).then((res) => {
            if (res.status === 200) {
                toLogin();
            } else { return alert(" ") };
        }).catch(err => {
            console.log(err);
        })
    }
    return (
        <>
            <div className='login' onSubmit={onSubmitHandler}>
                <form >
                    <div>
                        <input ref={refInput} type="text" name="name" placeholder='' value={name} onChange={onNameHandler} className="login-input" />
                    </div>
                    <div>
                        <input type="email" name="email" placeholder='' value={email} onChange={onEmailHandler} className="login-input" />
                    </div>
                    <div>
                        <input type="password" name="password" placeholder='' value={pw} onChange={onPasswordHandler} className="login-input" />
                    </div>
                    <div>
                        <input type="password" name="confirmPW" placeholder=' ' value={confirmPW} onChange={onConfirmPWHandler} className="login-input" />
                    </div>
                    <div><button type="submit" className="login-btn"> </button></div>
                </form>
            </div>
        </>
    );
}

export default Main